import React from 'react'
import './CustomNotie.scss'
import cx from 'classnames'
import { NotificationTypes } from 'Shared/constants/Notifications'
import PropTypes from 'prop-types'

const CustomNotie = ({ type, isVisible, children, onClose }) => {
  const containerClass = cx({
    'custom-notie__container': true,
    'custom-notie__visible': isVisible,
    'custom-notie__error': type === NotificationTypes.error,
    'custom-notie__success': type === NotificationTypes.success,
    'custom-notie__warning': type === NotificationTypes.warning
  })

  return (
    <div className={containerClass}>
      <div className='custom-notie__content'>
        <i className='icon-im-close custom-notie__close-mark' onClick={onClose} />
        {children}
      </div>
    </div>
  )
}

CustomNotie.propTypes = {
  type: PropTypes.string.isRequired,
  isVisible: PropTypes.bool.isRequired,
  onClose: PropTypes.func
}

export default CustomNotie



