import React, { useEffect } from 'react'
import { Form, Field } from 'redux-form'
import { Input, Title } from 'Shared/components/FormElements'
import { PrimaryBlockButton } from 'Shared/components/Button/index'
import withTranslations from 'Shared/hoc/withTranslations'
import LinkedIn from 'Shared/components/LinkedIn/LinkedIn'
import Google from 'Shared/components/Google/Google'
import AlternativesSeparator from 'Shared/components/AlternativesSeparator/AlternativesSeparator'
import NotificationService from 'Shared/services/NotificationService'
import { amplitudeEvents } from 'TrackingScripts/scripts/candidate/amplitude'
import { veroEvents } from 'TrackingScripts/scripts/candidate/vero'
import { getLocalData, removeLocalData } from 'Shared/helpers/session'
import mixpanel from 'mixpanel-browser'

const SignUp = (props) => {
  const { error, submitting, t, handleSubmit, redirectToComplete, loginWithLinkedIn, loginWithGoogle, isLoading } = props

  const trackSignUpButtonClick = () => {
    mixpanel.track(`Button Click - Create An Account - Sign Up Page`);
  };

  const trackLinkedInButtonClick = () => {
    mixpanel.track('Button Click - Linkedin - Sign Up Page');
  };

  const trackGoogleButtonClick = () => {
    mixpanel.track('Button Click - Google - Sign Up Page');
  };

  const handleLoginWithSocialFailed = (res) => {
    if (res.error === 'popup_closed_by_user') {
      console.error(res)
      NotificationService.error(t('alerts.loginWithSocialFailed'))
    }
  }

  const submitForm = values => {
    console.log(values)
    amplitudeEvents.startSignUp(values.email)
    veroEvents.startSignUp(values.email)
    redirectToComplete(values)
    removeLocalData('stripe_form_email')
  }

  const getButtonText = translationKey => submitting || isLoading ? t('authorization.signingUp') : t(`authorization.${translationKey}`)


  const signupFromStripePayment = () => {
    const hasEMail = getLocalData('stripe_form_email')
    if(!hasEMail) return
    if(hasEMail) handleSubmit(submitForm({email: hasEMail }))
  }



  useEffect(()=> {
    signupFromStripePayment()
    mixpanel.track("Page View - Sign Up")    
  }, [])
  

  return (
    <Form onSubmit={handleSubmit(submitForm)}>
      <Title text={t('authorization.signUp')} />
      <p className='form__text form__text--sign-up'>{t('authorization.candidateSignUpCTA')}</p>
      <Field
        component={Input}
        label={t('forms.email.label')}
        name='email'
        placeholder={t('forms.email.placeholder')}
        type='email'
      />
      <PrimaryBlockButton
        className='button--submit'
        disabled={error || submitting || props.isLoading}
        type='submit'
        block
        onClick={trackSignUpButtonClick}
      >
        {getButtonText('createAccount')}
      </PrimaryBlockButton>
      <AlternativesSeparator text={t('authorization.orSignupWith')} />
      <div className='columns'>
        <div className='column col-6 col-sm-12 button--linked-in__wrapper'>
          <LinkedIn
            disabled={props.isLoading}
            clientId={process.env.REACT_APP_LINKED_IN_CLIENT_KEY}
            text={getButtonText('linkedIn')}
            callback={() => {
              loginWithLinkedIn();
              trackLinkedInButtonClick();
            }}
            onClick={trackLinkedInButtonClick}
          />
        </div>
        <div className='column col-6 col-sm-12'>
          <Google
            clientId={process.env.REACT_APP_GOOGLE_CLIENT_ID}
            onSuccess={() => {
              loginWithGoogle();
              trackGoogleButtonClick();
            }}
            onFailure={handleLoginWithSocialFailed}
            disabled={props.isLoading}
            text={getButtonText('google')}
            onClick={trackGoogleButtonClick}
          />
        </div>
      </div>
    </Form>
  )
}

export default withTranslations(SignUp)



