/* eslint-disable no-throw-literal */
import { t } from 'i18next'
import isEmail from 'validator/lib/isEmail'
import { isPhoneNumber } from 'Shared/helpers/validator'
import * as Validation from 'Shared/constants/Validation'
import APIErrorMessages from 'Shared/constants/APIErrorMessages'

const validate = values => {
  const errors = {}
  const requiredFields = ['email', 'password', 'firstName', 'lastName', 'phoneNumber', 'dateOfBirth', 'hasAcceptedUnderageAgreement']

  requiredFields.forEach(field => {
    if (!values[field]) {
      errors[field] = {
        key: 'errors.requiredField',
        options: {
          field: t(`forms.${field}.label`)
        }
      }
    }
  })

  if (values.email && !isEmail(values.email)) {
    errors.email = {
      key: 'errors.invalidEmail',
      options: {}
    }
  }

  if (values.phoneNumber && !isPhoneNumber(values.phoneNumber)) {
    errors.phoneNumber = {
      key: 'errors.invalidPhoneNumber',
      options: {}
    }
  }

  if (values.password && values.password.length < Validation.PASSWORD_LENGTH) {
    errors.password = {
      key: 'errors.passwordTooShort',
      options: {
        chars: Validation.PASSWORD_LENGTH
      }
    }
  }

  if (values['hasAcceptedTermsAndPolicies'] !== true) {
    errors.hasAcceptedTermsAndPolicies = {
      key: 'errors.termsAgree'
    }
  }

  if (values['hasAcceptedUnderageAgreement'] !== true) {
    errors.hasAcceptedUnderageAgreement = {
      key: 'errors.underageAgreeRequired'
    }
  }

  return errors
}

const verifyUserEmail = verifyMethod => {
  return async (value) => {
    if (!value.email) {
      throw { email: {
        key: 'errors.emailNotProvided'
      } }
    }
    try {
      await verifyMethod(value.email)
      return false
    } catch (e) {
      const { name } = e.response.data.error
      switch (name) {
        case APIErrorMessages.EMAIL_ALREADY_TAKEN:
          throw { email: {
            key: 'errors.emailAlreadyTaken'
          } }
        default:
          throw { email: {
            key: 'errors.emailNotProvided'
          } }
      }
    }
  }
}

export {
  validate,
  verifyUserEmail
}



