import React, { useState } from 'react'
import Modal from 'Shared/components/Modal'
import { t } from 'i18next'
import RadioWrapper, { Layouts } from 'Shared/components/FormElements/RadioWrapper/RadioWrapper'
import Radio from 'Shared/components/FormElements/Radio/Radio'
import './SelectOptionConfirmation.scss'
import { cond, T, equals } from 'ramda'

function SelectOptionConfirmation (props) {
  const [value, setValue] = useState('accept')

  const radioProps = optionValue => (
    {
      input: {
        onChange: () => setValue(optionValue),
        value
      },
      meta: { touched: false, error: false, submitting: false }
    }
  )

  const getSelectedClass = optionValue => value === optionValue && 'select-option-confirmation__radio-selected'

  const handleSubmit = () => cond([
    [equals('accept'), props.handleAccept],
    [equals('decline'), props.handleDecline],
    [T, () => {}]
  ])(value)

  return (
    <Modal
      title={props.title}
      handleSubmit={handleSubmit}
      handleClose={props.handleClose}
      handleCancel={props.handleClose}
      show={props.show}
      cancelText={t('common.cancel')}
      submitText={t('common.continue')}
      closeOnSubmit={false}
      closeOnCancel={false}
      loading={props.isLoading}
    >
      <div className='select-option-confirmation__description'>
        {props.description}
      </div>
      <div className='select-option-confirmation__radio-container'>
        <RadioWrapper layout={Layouts.vertical}>
          <Radio
            className={getSelectedClass('accept')}
            {...radioProps('accept')}
            radioButtonValue='accept'
            text={props.acceptText}
          />
          <Radio
            className={getSelectedClass('decline')}
            {...radioProps('decline')}
            radioButtonValue='decline'
            text={props.declineText}
          />
        </RadioWrapper>
      </div>
    </Modal>
  )
}

export default SelectOptionConfirmation



