import { reduxForm } from 'redux-form'
import { withRouter } from 'react-router-dom'
import { connect } from 'react-redux'
import SignUp from 'Candidate/forms/Registration/SignUp'
import UserService from 'Shared/services/UserService'
import { validate, verifyUserEmail } from 'Candidate/connectors/Forms/Registration/CandidateCompleteRegistrationValidator'
import { pickFromQueryString } from 'Shared/helpers/string'
import { remapGoogleResponse, remapLinkedInResponse } from 'Shared/helpers/forms'
import { loginWithLinkedIn, loginWithGoogle } from 'Shared/ducks/user'
import { getLocalData } from 'Shared/helpers/session'

const userService = new UserService()

export const formName = 'CandidateSignUpForm'

let Form = reduxForm({
  form: formName,
  validate,
  asyncValidate: verifyUserEmail(userService.verifyCandidateEmail),
  shouldAsyncValidate: () => true,
  asyncBlurFields: []
})(SignUp)

const mapStateToProps = (state) => {
  const hasStripeFormEmail = getLocalData('stripe_form_email');
  const { email } = pickFromQueryString(['email'], window.location.search)
  const decodedEmail = email ? decodeURIComponent(email) : hasStripeFormEmail ? hasStripeFormEmail : ''

  return { initialValues: { email: decodedEmail }, isLoading: state.user.isLoading }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  const socialRedirectPath = isNewAccount => isNewAccount ? '/candidate/thanks' : '/candidate/dashboard'

  return ({
    loginWithLinkedIn: async values => {
      const mappedValues = remapLinkedInResponse(values)
      const { isNewAccount } = await dispatch(loginWithLinkedIn(mappedValues))
      return ownProps.history.push(socialRedirectPath(isNewAccount))
    },
    loginWithGoogle: async values => {
      const mappedValues = remapGoogleResponse(values)
      const { isNewAccount } = await dispatch(loginWithGoogle(mappedValues))
      return ownProps.history.push(socialRedirectPath(isNewAccount))
    }
  })
}

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(Form))



