import { reduxForm, formValueSelector } from 'redux-form'
import { connect } from 'react-redux'
import CompleteRegistration from 'Candidate/forms/Registration/CompleteRegistration'
import { validate } from 'Candidate/connectors/Forms/Registration/CandidateCompleteRegistrationValidator'
import { fetchCurrentLocationIfNeeded } from 'Shared/ducks/geolocation'
import { isAutoApprovalEnabled } from 'Shared/selectors/geolocation'
import { scrollToFirstError } from 'Shared/helpers/scroll'

export const formName = 'CompleteRegistration'

let Form = reduxForm({
  form: formName,
  onSubmitFail: scrollToFirstError,
  validate
})(CompleteRegistration)

const mapStateToProps = (state, ownProps) => ({
  dateOfBirthValue: formValueSelector(formName)(state, 'dateOfBirth'),
  isAutoApprovalEnabled: isAutoApprovalEnabled(state),
  initialValues: {
    email: ownProps.email
  }
})

const mapDispatchToProps = dispatch => ({
  fetchCurrentLocationIfNeeded: () => dispatch(fetchCurrentLocationIfNeeded())
})

export default connect(mapStateToProps, mapDispatchToProps)(Form)



